@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

:root {
  --color-bgdarkBlue: #1f1f38; /* colorbg */
  --color-bglightBlue: #2c2c6c; /* colorbgVariant*/
  --color-bluePrimary: #4db5ff; /* colorPrimary */
  --color-blueVariant: rgba(77, 181, 255, 0.4); /* colorPrimaryVariant */
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 400ms ease;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Poppins', sans-serif;
  /* background-image: url('./assets/bg-texture.png'); */
  /* background-color: var(--color-bgdarkBlue); */
  /* background-color: #a50c0e; */
  background-color: #fcfaf6;
  color: var(--color-white);
}

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
  color: #000;
  text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.2),
    0px -5px 10px rgba(255, 255, 255, 0.15);
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: rgba(0, 0, 0, 0.6);
  text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.2),
    0px -5px 10px rgba(255, 255, 255, 0.15);
}

section > h2 {
  /* color: var(--color-bluePrimary); */
  color: black;
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  /* color: var(--color-bglightBlue); */
  transition: all 400ms ease;
  color: var(--color-bglightBlue);
}

a:hover {
  /* color: var(--color-bluePrimary); */
  color: darkviolet;
}

.btn {
  width: max-content;
  display: inline-block;
  color: black;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  /* border: 1px solid black; */
  background-color: white;
  transition: var(--transition);
  box-shadow: 1px 1.9px 1.9px hsl(0deg 0% 0% / 0.47);
}

.btn:hover {
  background-color: var(--color-bglightBlue);
  color: white;
}

.btn-primary {
  background-color: white;
  color: black;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  .section {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  .section > h2 {
    margin-bottom: 2rem;
  }
}
