header {
  padding-top: 7rem;
  overflow: hidden;
  height: 100vh;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.imghead {
  box-shadow: 0.6px 1.3px 1.3px hsl(0deg 0% 0% / 0.48);
  box-shadow: 8px 16px 16px hsl(0deg 0% 0% / 0.25);
}

.text-shadow {
  color: rgba(0, 0, 0, 0.6);
  text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.2),
    0px -5px 10px rgba(255, 255, 255, 0.15);
}

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

/* .header__social {
  box-shadow: 0.8px 1.6px 1.6px hsl(0deg 0% 0% / 0.48);
} */

.header__socials::after {
  content: '';
  width: 1px;
  height: 2rem;
  background-color: indigo;
}

.hero {
  /* background: linear-gradient(#4db5ff, transparent); */
  width: 22rem;
  position: absolute;
  left: calc(50% - 11rem);
  /* left: 50%;
  transform: translateX(-50%); */
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
  overflow: hidden;
}

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

@media screen and (max-width: 1024px) {
  header {
    height: 64vh;
  }
}

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
}
