footer {
  /* background: var(--color-bluePrimary); */
  background: whitesmoke;
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
  border: 1.5px solid black;
}
footer a {
  color: var(--color-bgdarkBlue);
}
.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}
.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}

.footer__socials a {
  background: var(--color-bgdarkBlue);
  color: var(--color-white);
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
  padding: 0.8rem;
}

.footer__socials a:hover {
  background: transparent;
  color: var(--color-bgdarkBlue);
  border-color: var(--color-bgdarkBlue);
}

.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-bgdarkBlue);
}

@media screen and (max-width: 1024px) {
}

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.5rem;
  }
  .footer__socials {
    margin-bottom: 2.6rem;
  }
}
