/* container split 2 */
.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

/* box behind img */
.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  /* height=width */
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    /* var(--color-bluePrimary), */ transparent
  );
  place-items: center;
  /* border: 1px solid orange; */
}

/* hero image */

.about__me-img {
  border-radius: 1rem;
  overflow: hidden;
  /* transform: rotate(0deg); */
  transition: var(--transition);
  box-shadow: 7.2px 14.4px 14.4px hsl(0deg 0% 0% / 0.28);
}

.about__me-img:hover {
  transform: rotate(10deg);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about__card {
  padding: 2rem;
  /* background: var(--color-bglightBlue); */
  background-color: white;
  box-shadow: 7.2px 14.4px 14.4px hsl(0deg 0% 0% / 0.28);
  border: 1.5px solid transparent;
  border-radius: 1rem;
  text-align: center;
  transition: var(--transition);
}

.about__card:hover {
  /* background: ; */
  border-color: var(--color-bglightBlue);
  cursor: default;
}

.about__icon {
  color: indigo;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about__card h5 {
  font-size: 0.95rem;
}

.about__card small {
  font-size: 0.7rem;
  /* color: var(--color-light); */
  color: black;
}

.about__content p {
  margin: 2rem 0 2.6rem;
  /* color: var(--color-light);
   */
  color: rgba(0, 0, 0, 0.6);
  text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.2),
    0px -5px 10px rgba(255, 255, 255, 0.15);
}

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about__content p {
    margin: 1rem 0 1.54rem;
  }
}

@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .about__content {
    text-align: center;
  }
  .about__content p {
    margin: 1.5rem 0;
  }
}
