.container.contact__container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
}

.contact__options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.contact__option {
  background: white;
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
  box-shadow: 7.2px 14.4px 14.4px hsl(0deg 0% 0% / 0.28);
}

.contact__option:hover {
  background: transparent;
  /* background-color: var(--color-bglightBlue); */
  border-color: var(--color-bglightBlue);
}

.contact__option-icons {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: slateblue;
}

.contact__option-icons a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: whitesmoke;
  border: 1px solid black;
  resize: none; /* for text box resize */
  color: black;
  /* box-shadow: 0.6px 1.3px 1.3px hsl(0deg 0% 0% / 0.48); */
  box-shadow: 1.8px 3.7px 3.7px hsl(0deg 0% 0% / 0.44);
}

@media screen and (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

@media screen and (max-width: 600px) {
  .container.contact__container {
    width: var(--container-width-sm);
  }
}
